<template>
  <div class="page-container">
    <client-data />
    <project-data />
    <div class="page-box">
      <div class="page-box-title">
        <h3>
          Ruimtes
        </h3>
        <div class="flex-divider"></div>
        <base-button
          @click="openCreateModal"
          class="small-on-mobile"
        >
          <base-icon
            iconRef="plus"
            position="left"
          /><span class="button-label">Nieuwe Ruimte</span>
        </base-button>
      </div>
      <div class="page-box-datatable-container">
        <rooms-data-list
          :dataList="currentProjectRooms"
          @click="dataEntryClick($event)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import clientData from "./client-page-components/client-data";
import projectData from "./client-page-components/project-data";
import roomsDataList from "./data-list-components/rooms-data-list.vue";

export default {
  name: "Project",
  components: {
    clientData,
    projectData,
    roomsDataList
  },
  computed: {
    currentProject: function() {
      return this.$store.getters.getProjectsList.find(project => {
        return project.id == this.$route.params.projectId;
      });
    },
    currentClient: function() {
      return this.$store.getters.getClientsList.find(client => {
        return client.id == this.currentProject.data().belongsTo;
      });
    },
    currentProjectRooms: function() {
      return this.$store.getters.getRoomsList.filter(room => {
        return (
          room.data().belongsTo == this.currentProject.id &&
          room.data().trashed == false
        );
      });
    }
  },
  methods: {
    openCreateModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "room",
        modalType: "create",
        shown: true
      });
    },
    dataEntryClick: function(value) {
      this.openRoom(value);
    },
    openRoom: function(targetRoomId) {
      this.$router.push(`/ruimte/${targetRoomId}`);
    }
  },
  watch: {
    currentClient: function() {
      this.$store.commit("setCurrentClientPageClient", this.currentClient);
    },
    currentProject: function() {
      this.$store.commit("setCurrentClientPageProject", this.currentProject);
    }
  },
  mounted: function() {
    this.$store.commit("setCurrentClientPage", {
      client: this.currentClient,
      project: this.currentProject
    });
    this.$setPageTitle(`Project - ${this.currentProject.data().projectName}`);
  },
  destroyed: function() {
    this.$store.commit("setCurrentClientPage", {});
  }
};
</script>

<style>
</style>



